header {
  /* background-color: #000; */
  text-align: center;
  padding: 8rem;
}

header h2 {
  padding: 0 0;
  margin: 0.2em 0 0.2em;
  font-family: "Roboto", sans-serif;
  font-size: 4vw;
  /* color: #989898; */
}

header h1 {
  padding: 0 0;
  margin: 0.2em 0 0.2em;
  font-size: 4vw;
  font-family: "Courier New";
  opacity: 0.8;
  /* color: #fff; */
  display: block;
}

header h1 > span {
  color: rgb(253, 215, 1);
}

header h3 {
  margin: 1em;
  font-family: "Roboto", sans-serif;
  font-size: 2vw;
  /* color: #808080; */
}

.avatar {
  vertical-align: middle;
  width: 15vw;
  border-radius: 50%;
}

.fa {
  padding: 2rem 4rem 2rem 2rem;
  font-size: 3rem;
  width: 2rem;
  text-align: center;
  text-decoration: none;
  margin: 1rem 0.2rem;
}

.fa:hover {
  opacity: 0.7;
}

.fa-github {
  background: #fff;
  color: #000;
}

.fa-linkedin {
  background: #007bb5;
  color: #fff;
}
